var that;
var oneVideo;
export default {
	data() {
		return {
			list: [],
			page: 1,
			course_id: '',
			play: false,
			playsrc: ''
		};
	},
	methods: {
		getlist(e) {
			that.$request
				.request({
					url: that.api.college.course_detail,
					data: {
						course_id: that.course_id
					},
					method: 'get'
				})
				.then(res => {
					let list = res;
					that.list = list;
				});
		},
		tpinfo(e) {
			let src = e.currentTarget.dataset.src;
			that.playsrc = src;
			console.log(e);
			console.log(that.playsrc);
			oneVideo.requestFullScreen();
			setTimeout(function() {
				oneVideo.play();
			}, 1000);

			// uni.navigateTo({
			// 	url:'/pages/academy/videodetail/videodetail?id='+e.currentTarget.dataset.id
			// })
		},
		fullscreenchange(e) {
			console.log(e.detail.direction);
		}
	},
	onLoad(option) {
		that = this;
		that.course_id = option.course_id ? option.course_id : '8';
		that.getlist();
	},
	onReady() {
		oneVideo = uni.createVideoContext('oneVideo');
	}
};
